* {
 	margin: 0;
	padding: 0;
	box-sizing:border-box
}

body {
	box-sizing: border-box;
	font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
	background-color: #151515;
	color: whitesmoke;
}

h1 {
	margin: 10px;
}

img {
  width: 100%;
}

.container {
	margin: 8px;
}

/*search bar section*/
.segment {
	font-size: 1rem;
	padding: 12px;
	border: 1px solid rgba(34,36,38,.15);
	background-color: #3c3b3b;	
}

.search-bar {
	margin-bottom: 8px;
    border-radius: 5px;
    box-shadow: 0 0px 5px 0 rgba(90, 90, 90, 0.1), 0 0px 10px 0 rgba(90, 90, 90, 0.1);
	border: 1px solid rgba(0, 0, 0, 0.5);
}

.field {
	width: 100%;
}



.field label {
	font-size: 14px;
	padding-left: 4px; 
}

.field input {
	width: 100%;
	margin-top: 4px;
	padding: 10px 6px;
}

/*video section*/
.video-section-wrapper {
	display: grid;
}

@media screen and (min-width: 100px) {
  .video-section-wrapper {
	grid-template-columns: 1fr;
	grid-gap: 8px;
  }
}

@media screen and (min-width: 950px) {
  .video-section-wrapper {
	grid-template-columns: 1fr 275px;
	grid-gap: 10px;
  }
}

@media screen and (min-width: 1050px) {
  .video-section-wrapper {
	grid-template-columns: 1fr 350px;
	grid-gap: 10px;
  }
}

@media screen and (min-width: 1100px) {
  .video-section-wrapper {
	grid-template-columns: 1fr 350px;
	grid-gap: 10px;
  }
}

@media screen and (min-width: 1150px) {
  .video-section-wrapper {
	grid-template-columns: 1fr 400px;
	grid-gap: 10px;
  }
}

.videodetail-col {
    border-radius: 5px;
    box-shadow: 0 0px 5px 0 rgba(90, 90, 90, 0.1), 0 0px 10px 0 rgba(90, 90, 90, 0.1);
	border: 1px solid rgba(0, 0, 0, 0.5);
}

.iframe-container {
	overflow: hidden;
/*  // Calculated from the aspect ratio of the content (in case of 16:9 it is 9/16= 0.5625)
*/
	padding-top: 56.25%;
	position: relative;
}

.iframe-container iframe {
	border: 0;
	height: 100%;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
	border-radius: 7px;
}

.video-detail-description {
	padding-top: 10px; 
}

.video-detail-description .header {
	margin-bottom: 5px;
	font-size: 1.1rem;
}

.videolist-col {
    box-shadow: 0 0px 5px 0 rgba(90, 90, 90, 0.1), 0 0px 10px 0 rgba(90, 90, 90, 0.1);
    border-radius: 5px;
	border: 1px solid rgba(0, 0, 0, 0.5);

}

.videolist-col > div:not(:first-child) {
	padding-top: .5rem;
	display: flex;
	align-items: center;
}


.video-item {
	display: flex !important;
	align-items: center !important;
	cursor: pointer;
	flex-wrap: nowrap;
	padding-bottom: .3rem;
	border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}

.videolist-col > div:last-child {
	border-bottom: none;
}

.video-item-imgbox {
	width: 40%;
}

.video-item-imgbox img {
	border: 1px solid rgba(0, 0, 0, 0.3);
}

.video-item-txtbox {
	width: 60%;
	padding-left: .5rem;
	font-size: .9rem;

}
.video-item-title {
	min-width: 100px;
}